.button{border:1px solid #0eb553;width:224px;height:40px;border-radius:8px;color:#0eb553;transition:all .3s}.button:hover{background-color:#0eb553;color:#fff;transition:all .3s}@media(max-width: 1199px){.button:hover{background-color:rgba(0,0,0,0);color:#0eb553}}@media(max-width: 575px){.button{font-size:14px}}